<template>
    <el-card class="box-card">
        <div class="list_head" slot="header">
            <span>角色列表</span>
            <el-button v-permission="'system:role:create'" @click="insertDialogVisible = true" type="primary"
                icon="el-icon-circle-plus-outline" plain size="mini">添加角色</el-button>
        </div>
        <el-table :data="tableData">
            <el-table-column prop="role_id" label="#">
            </el-table-column>
            <el-table-column prop="role_name" label="角色名称">
                <template slot-scope="scope">
                    <el-tag type="primary">{{ scope.row.role_name }}</el-tag>
                </template>
            </el-table-column>
            <el-table-column prop="role_description" label="角色描述">
            </el-table-column>
            <el-table-column prop="role_code" label="角色代码">
            </el-table-column>
            <el-table-column label="操作" label-width="200">
                <!-- template插槽 内部放复杂结构 必须加slot-scope="scope"-->
                <template slot-scope="scope">
                    <el-button v-permission="'system:role:edit'" @click="openEditDialog(scope.row)" type="primary"
                        :disabled="scope.row.role_id == 1" icon="el-icon-edit" plain size="mini"></el-button>
                    <el-button v-permission="'system:role:remove'" @click="handleRemove(scope.row.role_id, scope.$index)"
                        :disabled="scope.row.role_id == 1" plain size="mini" icon="el-icon-delete"
                        type="danger"></el-button>
                    <el-link :href="`#/system/role/${scope.row.role_id}`">
                        <el-button v-permission="'system:role:auth'" plain size="mini" icon="el-icon-setting" type="danger"
                            :disabled="scope.row.role_id == 1"></el-button>
                    </el-link>
                </template>
            </el-table-column>
        </el-table>

        <!-- 下面两个弹窗结构是相同的，可以封装成一个，但是耦合度高，不易于阅读 -->
        <!-- 添加 -->
        <el-dialog title="添加角色" :visible.sync="insertDialogVisible" @close="handleCloseDialog('insertForm')">
            <el-form :model="insertForm" :rules="rules" ref="insertForm" status-icon label-width="100px">
                <el-form-item label="角色名称" prop="role_name">
                    <el-input type="text" v-model="insertForm.role_name" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="角色描述" prop="role_description">
                    <el-input type="text" v-model="insertForm.role_description" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="角色代码" prop="role_code">
                    <el-input type="text" v-model="insertForm.role_code" autocomplete="off"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="insertDialogVisible = false">取 消</el-button>
                <!-- scope.row 为这一行的数据 -->
                <el-button @click="handleInsert" type="primary">添 加</el-button>
            </span>
        </el-dialog>

        <!-- 编辑 -->
        <el-dialog title="编辑角色" :visible.sync="editDialogVisible" @close="handleCloseDialog('editForm')">
            <el-form :model="editForm" :rules="rules" ref="editForm" status-icon label-width="100px">
                <el-form-item label="角色名称" prop="role_name">
                    <el-input type="text" v-model="editForm.role_name" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="角色描述" prop="role_description">
                    <el-input type="text" v-model="editForm.role_description" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="角色代码" prop="role_code">
                    <el-input type="text" v-model="editForm.role_code" autocomplete="off"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="editDialogVisible = false">取 消</el-button>
                <el-button @click="handleEdit" type="primary">保 存</el-button>
            </span>
        </el-dialog>
    </el-card>
</template>

<script>
import { Role } from '@/api';
// 注意：有时候打出大写函数，会自动添加一些module，删去即可。

export default {
    name: 'List',
    data() {
        return {
            tableData: [],
            insertDialogVisible: false,
            insertForm: {
                role_name: '',
                role_description: '',
                role_code: '',
            },
            editDialogVisible: false,
            editForm: {
                role_name: '',
                role_description: '',
                role_code: '',
            },
            // 缓存当前编辑的数据 -- 指针地址
            current_role: '',
            rules: {
                role_name: [
                    { required: true, message: '请输入角色名称！', trigger: 'blur' },
                ],
                role_description: [
                    { required: true, message: '请输入角色描述！', trigger: 'blur' },
                ],
                role_code: [
                    { required: true, message: '请输入角色代码！', trigger: 'blur' },
                ],
            },
        }
    },
    created() {
        this.loadList();
    },
    methods: {
        //获取文章列表 -- 发送Ajax
        async loadList() {
            let { status, msg, data } = await Role.list();

            if (status) {
                this.tableData = data;
            }
        },
        // dialog组件的事件方法 ===> 所以点击事件写在组件上 ==> 去除上次表单验证的效果
        handleCloseDialog(formName) {
            // this.$refs.this.insertForm.resetFields();
            // 因为页面有两个表单，所以使用传参的方法。
            this.$refs[formName].resetFields();
        },
        // 添加角色
        handleInsert() {
            // 通过$resfs 找到 insertForm
            this.$refs.insertForm.validate(async (valid) => {
                if (valid) {
                    let { status, msg, data } = await Role.create({ ...this.insertForm });
                    if (status) {
                        // 添加角色 
                        // 因为 insertForm 为单层对象，所以浅拷贝即可，若是嵌套数组或者嵌套对象则需要深拷贝。
                        // 后台返回的 data数据 中 有 role_id 
                        this.tableData.push({ ...this.insertForm, ...data });
                        // 关闭弹窗
                        this.insertDialogVisible = false;
                        // 信息提示
                        this.$message.success(msg);
                    } else {
                        this.$message.error(msg);
                    }
                }
            })
        },
        // 打开编辑弹窗
        openEditDialog(role_data) {
            // 缓存 scope.row 的指针地址 ==> 所以 current_tag 改变的话 ，tag_data也会变。
            this.current_role = role_data;
            // 还原表单数据 ==> 把表单数据还原到 编辑弹窗 中。
            // 此处浅拷贝 ==> 断开与表格的数据的双向绑定 ==> 不然修改时未点击保存，表格数据也会随之变化。
            this.editForm = { ...role_data };
            // 打开弹窗
            this.editDialogVisible = true;
        },
        // 编辑角色
        async handleEdit() {
            let { role_id } = this.editForm;
            // 表单验证的第二种写法 ==> 参考 Element UI ==> validate中不传入参数(即回调函数)时
            // 则会返回一个 promise ==> 可使用 try catch 捕获成功与失败 
            try {
                let valid = await this.$refs.editForm.validate();
                if (valid) {
                    let { status, msg } = await Role.edit(role_id, { ...this.editForm });
                    if (status) {
                        // 保存修改后的角色 -- 操作DOM
                        // 需要让 vue 监测到我们修改了DOM -- 动态属性 -- set()/get()
                        // 而 Object.assign 合并对象时，就会调用 set()/get() 方法
                        // 调用目标对象的set方法修改（合并）数据到目标对象，调用源对象的get方法获取源对象数据
                        Object.assign(this.current_role, this.editForm);
                        // 关闭弹窗
                        this.editDialogVisible = false;
                        // 信息提示
                        this.$message.success(msg);
                    } else {
                        this.$message.error(msg);
                    }
                }
            } catch (error) {
                console.log(error);
            }
        },
        // 删除角色
        handleRemove(id, i) {
            // 查阅文档 -- $confirm 返回一个 promise 对象 ==> 所以用 .then .catch 分别捕获。
            // 也可以 使用 async await ==> try catch 捕获
            this.$confirm('此操作将永久删除该角色, 是否继续?', '确认删除', {
                confirmButtonText: '删除',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(async () => {
                let { status, msg } = await Role.remove(id);
                if (status) {
                    // 后台数据已经删除 -- 但是页面并未更新 -- 需要操作DOM -- 删除
                    this.tableData.splice(i, 1);
                    // 信息提示
                    this.$message.success(msg);
                } else {
                    this.$message.error(msg);
                }
            }).catch(() => {
                this.$message.info('已取消删除');
            });
        },
    }
}
</script>

<style lang="less" scoped>
.el-card {
    width: 1200px;
}

.list_head {
    display: flex;
    justify-content: space-between;
}
</style>
